import Vue from 'vue';
import App from './App.vue';
import MintUI from 'mint-ui'
import router from './router';
import 'mint-ui/lib/style.css'
import './assets/stylesheets/_mint.scss'
import './util/rem';

Vue.use(MintUI)

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



